import React, { Component } from 'react'
import {Link} from 'react-router-dom'
class Aside extends Component {
    constructor(props) {
        super(props)
        this.state={
            ind:0
        }
    }
    
    render () {
        let {aside} = this.props;
        let {id} = this.state;
        return <div className="nav">
            {
                aside && aside.map((item,key)=>{
                    return <Link className={id===key?'action':''} key={key} to={item.path} onClick={()=>{
                        this.onAction(key)
                    }}>{item.text}</Link>
                })
            }
        </div>
    }
    onAction=(key)=>{
        this.setState({
            id:key
        })
    }
}

export default Aside