import React, { Component } from 'react'
import {Redirect} from 'react-router-dom'
import TipBlock from '../../common/TipBlock'
class Shouye extends Component {
    // isLogin(push){
    //     if(localStorage.getItem('user') !== 'success'){
    //         push('/login')
    //     }
    // }
    render () {
        let {history:{push},iconSrc,personInfo} = this.props;
        if(localStorage.getItem('user')!=='success'){
            return <Redirect to='/login'/>
        }
        return <div className='shou'>
            <div className="left">
                <img src={iconSrc}/>
                <p>{personInfo.name}</p>
                <p>岗位：{personInfo.gang}</p>
                <p>总积分：{personInfo.fen}</p>
                <p>积分排名：{personInfo.min}</p>
            </div>
            <div className="right">
                <div className="right-block">
                    <div>
                        <h3><span>考勤分析</span><span>本月|月</span></h3>
                        <dl>
                            <dt><img src="../../img/3.jpg" /></dt>
                            <dd>
                                <ul>
                                    <li>出勤 &nbsp; 0次</li>
                                    <li>请假 &nbsp; 0次</li>
                                    <li>出差 &nbsp; 0次</li>
                                    <li>迟到 &nbsp; 0次</li>
                                    <li>外出 &nbsp; 0小时</li>
                                    <li>加班 &nbsp; 0小时</li>
                                </ul>
                            </dd>
                        </dl>
                    </div>
                </div>
                <div className="right-block">
                    <div>
                        <h3><span>日报</span><span>本月|月</span></h3>
                        <dl>
                            <dt><img src="../../img/4.jpg" /></dt>
                            <dd>
                                <ul>
                                    <li>出勤 &nbsp; 0次</li>
                                    <li>请假 &nbsp; 0次</li>
                                    <li>出差 &nbsp; 0次</li>
                                    <li>迟到 &nbsp; 0次</li>
                                    <li>外出 &nbsp; 0小时</li>
                                    <li>加班 &nbsp; 0小时</li>
                                </ul>
                            </dd>
                        </dl>
                    </div>
                </div>
                <div className="right-block">
                    <div>
                        <h3><span>客户</span><span>本月|月</span></h3>
                        <dl>
                            <dt><img src="../../img/5.jpg" /></dt>
                            <dd>
                                <ul>
                                    <li>出勤 &nbsp; 0次</li>
                                    <li>请假 &nbsp; 0次</li>
                                    <li>出差 &nbsp; 0次</li>
                                    <li>迟到 &nbsp; 0次</li>
                                    <li>外出 &nbsp; 0小时</li>
                                    <li>加班 &nbsp; 0小时</li>
                                </ul>
                            </dd>
                        </dl>
                    </div>
                </div>
                <div className="right-block">
                    <div>
                        <h3><span>活动数据</span><span>本月|月</span></h3>
                        <dl>
                            <dt><img src="../../img/6.jpg" /></dt>
                            
                        </dl>
                    </div>
                </div>
            </div>
            <button onClick={()=>{
                localStorage.removeItem('user');
                push('/login')
            }}>退出</button>
            {/* <TipBlock /> */}
        </div>
    }
}
Shouye.defaultProps = {
    iconSrc:'../img/2.jpg',
    personInfo:{
        name:'Hi.Jim阳光',
        gang:'分布主管',
        fen:2000,
        min:'100/2500'
    }

}

export default Shouye