import {INIT_DATA} from '../action/home.js'
let types = {
    [INIT_DATA](state,action){
        state.initData = action.initData;
    }
}
let home=(state={msg:'',initData:[]},action)=>{
    types[action.type]&&types[action.type](state,action);
    return {...state}
}

export default home;