import React,{Component} from 'react';
class TipBlock extends Component{
    constructor(props) {
        super(props)
        this.state={
            title:'nicai'
        }
    }
    
    render(){
        //let {title} = this.props;
        let {title} = this.state;
        return <div className="right">
            <h3>{title}</h3>
            <div>
                {
                    this.props.children
                }
            </div>
        </div>
    }
    
}
TipBlock.defaultProps = {
    iconSrc:'../img/2.jpg',
    personInfo:{
        name:'Hi.Jim阳光',
        gang:'分布主管',
        fen:2000,
        min:'100/2500'
    }

}
export default TipBlock;