import React, { Component } from 'react'
import Aside from './aside'
class Content extends Component {
    render () {
        let {aside} =this.props;
        return <div className="content">
            <Aside aside={aside}/>
            <div className="right-content">
                {
                    this.props.children
                }
            </div>
        </div>
    }
}

export default Content