import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import RenderRoute from '../router/renderRoute'

class Layout extends Component {
    constructor(props) {
        super(props)
        this.state={
            id:0
        }
        this.props.history.push('/layout/shou')
    }
    
    componentDidMount () {
        
    }

    render () {
        let {id} = this.state;
        let {path} = this.props.match;
        let {routes,navList,logoSrct,user,info} = this.props;
        return <div className="wrap">
            <header>
                <h1><img src='../../img/1.jpg' /></h1>
                <nav>
                    {
                        navList.map((item,key)=>{
                            return <Link key={key} to={path+item.lu} className={id === key? 'action' :''} onClick={this.handleClick.bind(this,item,key)}>{item.text}</Link>
                        })
                    }
                    {/* <Link to={`${path}/shou`}>首页</Link>
                    <Link to={`${path}/guan`}>员工管理</Link>
                    <Link to={`${path}/kao`}>员工考勤</Link>
                    <Link to={`${path}/kao`}>工作计划</Link>
                    <Link to={`${path}/kao`}>客户管理</Link>
                    <Link to={`${path}/kao`}>亚太学院</Link>
                    <Link to={`${path}/kao`}>级别考核</Link>
                    <Link to={`${path}/kao`}>管理助手</Link>
                    <Link to={`${path}/kao`}>积分管理</Link> */}
                </nav>
                <span>Jim阳光</span>
                <span>消息栏</span>
            </header>
            <section>
                {/* <Route path={`${path}/shou`} component={Shou}/>
                <Route path={`${path}/guan`} component={Guan}/>
                <Route path={`${path}/kao`} component={Kao}/> */}
                <RenderRoute routes={routes}/>
            </section>
        </div>
    }
    handleClick(item,key){
        this.props.navAction(item,key)
        this.setState({
            id:key
        })
    }
}
Layout.defaultProps = {
    navList:[
        {text:'首页',lu:'/shou'},
        {text:'员工管理',lu:'/guan'},
        {text:'员工考清',lu:'/kao'},
        {text:'工作计划',lu:'/kao'},
        {text:'客户管理',lu:'/kao'},
        {text:'亚太学院',lu:'/kao'},
        {text:'级别考核',lu:'/kao'},
        {text:'管理助手',lu:'/kao'},
        {text:'积分管理',lu:'/kao'}
    ],
    navAction:(item,key)=>{
        console.log(item,key)
    },
    logoSrct:'../img/1.jpg',
    user:'Jim阳光',
    info:'通知栏'
}

export default Layout