import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import RenderRoute from '../../router/renderRoute'
import Content from '../../common/content'
class Guan extends Component {
    render () {
        let aside=this.props.routes.map(item=>{
            return {
                text:item.linkText,
                path:this.props.match.path+item.path
            }
        })
        return <Content aside={aside}>
            <div>
                <RenderRoute routes={this.props.routes} />
            </div>
        </Content>
        //  <div>
        //     {/* <Link to={`${this.props.match.path}/dang`}>档案管理</Link>
        //     <Link to={`${this.props.match.path}/fen`}>分配管理</Link> */}
            
        // </div>
    }
}

export default Guan