import Login from '../components/login'
import Layout from '../components/layout'
import Shou from '../components/layout/shouye'
import Kao from '../components/layout/kao'
import Guan from '../components/layout/guan'
import Dang from '../components/layout/guan/dang'
import Fen from '../components/layout/guan/fen'
import Dai from '../components/layout/guan/dai'
import Ke from '../components/layout/guan/ke'
import Li from '../components/layout/guan/li'
import Sheng from '../components/layout/guan/sheng'
import Xin from '../components/layout/guan/xin'
export default [
    {
        path:'/login',
        component:Login,
        linkText:'登录'
    },
    {
        path:'/layout',
        component:Layout,
        children:[
            {
                path:'/shou',
                component:Shou
            },
            {
                path:'/kao',
                component:Kao
            },
            {
                path:'/guan',
                component:Guan,
                linkText:'员工管理',
                children:[
                    {
                        path:'/dang',
                        component:Dang,
                        linkText:'档案管理'
                    },
                    {
                        path:'/fen',
                        component:Fen,
                        linkText:'分配管理'
                    },{
                        path:'/ke',
                        component:Ke,
                        linkText:'课程管理'
                    },
                    {
                        path:'/dai',
                        component:Dai,
                        linkText:'代课费管理'
                    },{
                        path:'/sheng',
                        component:Sheng,
                        linkText:'生日提醒'
                    },
                    {
                        path:'/xin',
                        component:Xin,
                        linkText:'薪酬管理'
                    },{
                        path:'/li',
                        component:Li,
                        linkText:'离职管理'
                    }
                ]
            }
        ]
    }
]