import {call,put,all} from 'redux-saga/effects'
import {takeEvery} from 'redux-saga'
import {INIT_DATA} from '../action/home'
// 任务saga
let url = 'http://localhost:8080/getAllStudentInfo';

function request(url){
    return fetch(url).then(response=>{
        return request.json();
    })
}

function* taskGetInitData(action){
    let initData = yield call(request,url);
    console.log(initData)
    yield put({type:INIT_DATA,initData});
}

function* taskAddData(){
    console.log('taskAdd')
}

// 监听saga
function* getInitData(){
    yield takeEvery('initSate',taskGetInitData);
    yield takeEvery('addData',taskAddData)
}


// rootSaga

function* rootSaga(){
    yield all([getInitData()])
}
export default rootSaga;